import * as moment from "moment";
import { Map, leftPad } from "@ay/util";

export type TableFilterFn = (value: any, condition: string) => boolean;
export const TableFilterFn = null;

export type TableFilter = TableFilterFn | "number" | "string" | "timestamp" | "moment";
export const TableFilter: Map<TableFilterFn> = {
  number: (value: any, condition: string) => {
    let OP_REGEX = /^(\>|\<|\=|\>\=|\<\=)(\d+\.?\d*)$/; //== >= <= > <
    let TO_REGEX = /^(\d+\.?\d*)\~(\d+\.?\d*)$/; // 10 ~ 20
    if (OP_REGEX.test(condition)) {
      return condition.replace(OP_REGEX, (substring: string, ...args: string[]) => {
        let [op, target] = args;
        let ret: boolean;
        switch (op) {
          case '>':
            ret = value > target;
            break;
          case '<':
            ret = value < target;
            break;
          case '>=':
            ret = value >= target;
            break;
          case '<=':
            ret = value <= target;
            break;
          case '=':
            ret = value == target;
            break;
          default:
            return 'false';
        }
        return ret.toString();
      }) === 'true';

    } else if (TO_REGEX.test(condition)) {
      return condition.replace(TO_REGEX, (substring: string, ...args: string[]) => {
        return (value >= args[0] && value <= args[1]).toString();
      }) === 'true';
    } else {
      return +value === +condition;
    }
  },
  string: (value: any, condition: string) => value.indexOf(condition) !== -1,
  moment: (value: moment.Moment, condition: string) => {
    let OP_REGEX = /^(\>|\<|\=|\>\=|\<\=)([\d]{0,4})([\/\-\:]*)([\d]{0,2})([\/\-\:]*)([\d]{0,2})$/;
    let TO_REGEX = /^([\d]{0,4})([\/\-\:]*)([\d]{0,2})([\/\-\:]*)([\d]{0,2})(\~)([\d]{0,4})([\/\-\:]*)([\d]{0,2})([\/\-\:]*)([\d]{0,2})$/;
    if (OP_REGEX.test(condition)) {
      return condition.replace(OP_REGEX, (substring: string, ...args: string[]) => {
        let numValue = 0;
        let numTarget = 0;
        args = args.filter(arg => arg != '');
        let op = args.shift();
        let ret = false;
        args.pop();
        if (args.length == 1) {
          numValue = +value.format("YYYY");
          numTarget = +(leftPad(args[0], 4, "0"));
        } else if (args[1] == '/' || args[1] == '-') {
          if (args.length == 3) { // MM/DD
            numValue = +value.format("MMDD");
            numTarget = +(leftPad(args[0], 2, "0") + leftPad(args[2], 2, "0"));
          } else if (args.length == 5) { // YYYY/MM/DD
            numValue = +value.format("YYYYMMDD");
            numTarget = +(leftPad(args[0], 4, "0") + leftPad(args[2], 2, "0") + leftPad(args[4], 2, "0"));
          } else {
            return "false";
          }
        } else {
          if (args.length == 3) { // HH:mm
            numValue = +value.format("HHmm");
            numTarget = +(leftPad(args[0], 2, "0") + leftPad(args[2], 2, "0"));
          } else if (args.length == 5) { // HH:mm:ss
            numValue = +value.format("HHmmss");
            numTarget = +(leftPad(args[0], 4, "0") + leftPad(args[2], 2, "0") + leftPad(args[4], 2, "0"));
          } else {
            return "false";
          }
        }

        switch (op) {
          case '>':
            ret = numValue > numTarget;
            break;
          case '<':
            ret = numValue < numTarget;
            break;
          case '>=':
            ret = numValue >= numTarget;
            break;
          case '<=':
            ret = numValue <= numTarget;
            break;
          case '=':
            ret = numValue == numTarget;
            break;
          default:
            return 'false';
        }
        return ret.toString();
      }) === "true";
    } else if (TO_REGEX.test(condition)) {
      return condition.replace(TO_REGEX, (substring: string, ...args: string[]) => {

        let numValue = 0;
        let numMin = 0;
        let numMax = 0;
        args = args.filter(arg => arg != '');
        args.pop();
        if (args.length == 3) {
          numValue = +value.format("YYYY");
          numMin = +(leftPad(args[0], 4, "0"));
          numMax = +(leftPad(args[2], 4, "0"));
        } else if (args[1] == '/' || args[1] == '-') {
          if (args.length == 7) { // MM/DD
            numValue = +value.format("MMDD");
            numMin = +(leftPad(args[0], 2, "0") + leftPad(args[2], 2, "0"));
            numMax = +(leftPad(args[4], 2, "0") + leftPad(args[6], 2, "0"));
          } else if (args.length == 11) { // YYYY/MM/DD
            numValue = +value.format("YYYYMMDD");
            numMin = +(leftPad(args[0], 4, "0") + leftPad(args[2], 2, "0") + leftPad(args[4], 2, "0"));
            numMax = +(leftPad(args[6], 4, "0") + leftPad(args[8], 2, "0") + leftPad(args[10], 2, "0"));
          } else {
            return "false";
          }
        } else {
          if (args.length == 7) { // HH:mm
            numValue = +value.format("HHmm");
            numMin = +(leftPad(args[0], 2, "0") + leftPad(args[2], 2, "0"));
            numMax = +(leftPad(args[4], 2, "0") + leftPad(args[6], 2, "0"));
          } else if (args.length == 11) { // HH:mm:ss
            numValue = +value.format("HHmmss");
            numMin = +(leftPad(args[0], 4, "0") + leftPad(args[2], 2, "0") + leftPad(args[4], 2, "0"));
            numMax = +(leftPad(args[6], 4, "0") + leftPad(args[8], 2, "0") + leftPad(args[10], 2, "0"));
          } else {
            return "false";
          }
        }
        return (numValue >= numMin && numValue <= numMax).toString();
      }) === "true";
    } else {
      return false;
    }
  },
  timestamp: (value: number, condition: string) => {
    return TableFilter.moment(moment(value), condition);
  }
}


